
import { computed, defineComponent } from "vue";
import baseTab from '@/shared/components/Tab/BaseTab.vue'
import basePan from '@/shared/components/Tab/BaseTabPan.vue'
import apm202001ss01 from "../apm202001ss01/apm202001ss01.vue";
import apm202001ss02 from "../apm202001ss02/apm202001ss02.vue";
import BreadCrumb from "@/shared/components/BreadCrumb/BreadCrumb.vue";

export default defineComponent({
  name: "apm202001sc00",
  components: {
    baseTab,
    basePan,
    apm202001ss01,
    apm202001ss02,
    BreadCrumb
  },

  data() {
    return {
      headers: [
        { name: "apm202001ss01.my_request" },
        { name: "apm202001ss02.my_approval" }
      ],
      breadcrumbs: [
        { value: 'apm201001sc00.label.home', keyPath: '/COM200001SC00', type: 'route' },
        { value: 'Portal Approval', keyPath: '', type: 'label' },
        { value: 'Portal Approval List', keyPath: '', type: 'label' },
      ],
      // currentTab: ''
      currentTab: 'apm202001ss01',
      search: {
				authorizationStatusCode: this.$route.query.filter ? String(this.$route.query.filter) : this.$route.query.status ? this.$route.query.status.toString() : '',
        searchTypeCode: this.$route.query.filter ? this.$route.query.filter.toString() != '01' ? '' : '01' : ''
			},
    };
  },

  provide(){
    return {
      searchTypeCode: computed(() => this.search.searchTypeCode)
    }
  },

  computed: {
    statusTypeCode(): { key: string, value: string }[] {
			return this.$store.state.CommonCodeModule.commonCode.AuthorizationStatusCode
		},
    routeType(): string {
      return this.$route.query.tab ? this.$route.query.tab.toString() : ''
    }
    // defaultActive(): string {
    //   if (this.$route.query.tab) {
    //     switch (this.$route.query.tab.toString()) {
    //       case 'my-request':
    //         return 'apm202001ss01.my_request'
    //       case 'my-approval':
    //         return 'apm202001ss02.my_approval'
    //     }
    //   }
    //   return 'apm202001ss01.my_request'
    // }
  },

  async mounted() {
    await this.$router.isReady().then(()=>{
      this.defaultActive()
    })
  },

  methods: {
    requestMyRequestLists(isSearch: boolean){
      type componentType = typeof apm202001ss01
      (this.$refs[this.currentTab] as InstanceType<componentType>).requestMyRequestLists(isSearch, this.search.authorizationStatusCode, '')
    },
    requestMyApprovalData(isSearch: boolean){
      type componentType = typeof apm202001ss02
      (this.$refs[this.currentTab] as InstanceType<componentType>).requestMyApprovalData(isSearch, this.search.authorizationStatusCode, this.search.searchTypeCode)
    },

    defaultActive() {
      switch (this.routeType) {
        case 'my-request':
          this.currentTab = 'apm202001ss01'
          break;
        case 'my-approval':
          this.currentTab = 'apm202001ss02'
          break;
        default: 
          this.currentTab = 'apm202001ss01'
      }
    },
    onClickRefresh() {
      type componentType = typeof apm202001ss01 | typeof apm202001ss02
      (this.$refs[this.currentTab] as InstanceType<componentType>).handleClearSearch();
      this.search.authorizationStatusCode = ''
      this.search.searchTypeCode = ''
    },
    onClickSearch(tab: 'apm202001ss01' | 'apm202001ss02') {
      if(tab == 'apm202001ss01') {
        this.requestMyRequestLists(true)
      } else {
        this.requestMyApprovalData(true)
      }
    }
    // handleCurrentTab(v: string) {
    //   this.currentTab = v.split('.')[0]
    // }
  }
});
