
import { defineComponent } from "vue";
import { APM202001SS01_REQ, APM202001SS01_RES_Body, APM202001SS01_RES_List } from "@/shared/type/APM202001SS01.type";
import pagination from "@/shared/components/Pagination/Pagination.vue";
import { PAGE_SIZE } from "@/shared/constants/common.const";
import '@vuepic/vue-datepicker/dist/main.css'
import VueDatePicker from '@vuepic/vue-datepicker';
import { DataCenter } from "@/shared/services/DataCenter";

export default defineComponent({
	name: "apm202001ss01",
	components: {
		pagination,
		VueDatePicker
	},
	data() {
		return {
			myRequestLists: [] as APM202001SS01_RES_List[],
			search: {
				employeeNo: "",
				authorizationStatusCode: this.$route.query.filter ? String(this.$route.query.filter) : "",
				pageNumber: 1,
				pageSize: PAGE_SIZE,
				fromDate: '' as unknown,
				toDate: '' as unknown
			},
			totalCount: 0 as number,
			startIndex: 1 as number,
			isResetPage: true as boolean,
			headers: [
				'No.',
				'View',
				'Status',
				'Authorization No',
				'Authorization Request reason',
				'Branch/Dept',
				'Requested By',
				'Reason',
				'1st Approver',
				'2nd Approver',
				'3rd Approver',
				'4th Approver',
				'5th Approver',
				'Create Date',
			]
		}
	},

	computed: {

		formattedStartDate(): string {
			return this.search.fromDate ? this.$datetime.formatVueDatePicker(this.search.fromDate as Date) : ''
		},

		formattedEndDate(): string {
			return this.search.toDate ? this.$datetime.formatVueDatePicker(this.search.toDate as Date) : ''
		},
	},

	created() {
		if (this.$route.query.filter !== undefined && this.$route.query.tab !== undefined) {
			if (this.$route.query.tab && this.$route.query.filter != null) {
				if (this.$route.query.tab.toString() == 'my-request') {
					this.search.authorizationStatusCode = this.$route.query.filter.toString()
					this.requestMyRequestLists(true, this.search.authorizationStatusCode, '');
				} else {
					this.requestMyRequestLists(false, this.search.authorizationStatusCode, '');
				}
			}
		} else {
			this.requestMyRequestLists(false, this.search.authorizationStatusCode, '');
		}
	},

	methods: {
		async requestMyRequestLists(isSearch: boolean, authorizationStatusCode: string, searchTypeCode: string) {
			const body: APM202001SS01_REQ = {
				employeeNo: await DataCenter.getData('employeeNo') as string || '',
				authorizationStatusCode: isSearch ? authorizationStatusCode : "",
				pageNumber: isSearch ? 1 : this.search.pageNumber,
				pageSize: this.search.pageSize,
				fromDate: this.search.fromDate ? this.$datetime.convertDateToString(this.search.fromDate as Date) : '',
				toDate: this.search.toDate ? this.$datetime.convertDateToString(this.search.toDate as Date) : '',
			}
			const res = await this.$request.getRequest("APM20200114", body, false) as APM202001SS01_RES_Body

			if (res.header.result) {
				this.myRequestLists = res.body.list;
				this.totalCount = res.body.totalCount;
			}
		},
		handleClearSearch() {
			this.search.authorizationStatusCode = "";
			this.search.employeeNo = "";
			this.search.pageNumber = 1;
			this.search.pageSize = PAGE_SIZE;
			this.search.fromDate = ''
			this.search.toDate = ''
			this.requestMyRequestLists(false, this.search.authorizationStatusCode, '');
		},
		handlePagination(pageNumber: number) {
			this.isResetPage = false;
			this.search.pageNumber = pageNumber;
			this.requestMyRequestLists(false, this.search.authorizationStatusCode, '');
		},

		handleStartIndex(startIndex: number) {
			this.startIndex = startIndex;
		},

		handleView(data: APM202001SS01_RES_List) {
			if (data.linkScreenNo.length > 13) {
				this.$router.push({
					path: `/${data.linkScreenNo.slice(0, 13)}`,
					query: {
						approvalType: 'my-request',
						approvalStatus: data.authorizationStatusCode,
						authorizationNo: data.authorizationNo,
						employeeNo: data.authorizationRequestEmpNo,
						tab: `${data.linkScreenNo.slice(13, 26)}`
					}
				})
			} else {
				this.$router.push({
					path: `/${data.linkScreenNo}`,
					query: {
						approvalType: 'my-request',
						approvalStatus: data.authorizationStatusCode,
						authorizationNo: data.authorizationNo,
						employeeNo: data.authorizationRequestEmpNo,
					}
				})
			}
		},
		handleConvertAuthStatusCode(code: string): string {
			return this.$codeConverter.convertValue("AuthorizationStatusCode", code)
		},
	},
})

